import { FC } from 'react'
import { useState } from 'react'
import EditBot from './EditBot';
import ExampleBot from '../ExampleBot';
import Spinner from 'react-bootstrap/Spinner';

const Edit: FC = () => {

    const [buttonState, setButtonState] = useState(false)

    return (
        <>
            <div className='relative'>
                {buttonState && <div className="absolute w-full h-full flex items-center justify-center z-10"> <Spinner className="" /> </div>}
                <div className={"" + (buttonState && "backdrop-blur-md backdrop-brightness-50 bg-white/30")}>
                    <div className='flex flex-col md:flex-row justify-center items-center mb-6'>
                        <EditBot setButtonState={setButtonState} />
                        <ExampleBot />
                    </div>
                </div>
            </div>
        </>
    )
}

export default Edit;