import { FC } from 'react'
import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { loadStripe } from '@stripe/stripe-js';

const publicKey = process.env.REACT_APP_STRIPE_PUBLISH_KEY;
const secretKey = process.env.REACT_APP_STRIPE_SECRET_KEY;
console.log(secretKey)
const stripeCheckoutSession = require('stripe')(secretKey as string)

interface PlanModalProps {
    open: boolean;
    handleOpen: React.MouseEventHandler;
    handleClose: () => void;
}

const PlanModal : FC<PlanModalProps> = ({open, handleOpen, handleClose}) => {

    const onUpgradeNow = async () => {
        console.log("------------upgarde--------------------")
        const email = localStorage.getItem('auth_email')
        const checkoutSession = await stripeCheckoutSession.checkout.sessions.create({
          line_items: [
            {
              price_data: {
                currency: 'USD',
                product_data: {
                  name: "Label Purchase",
                },
                unit_amount: 100,
              },
              quantity: 20,
            },
          ],
          mode: "payment",
          success_url: 'https://chatsavvy.ai/',
          cancel_url: 'https://chatsavvy.ai/',
          customer_email: email,
        });
        console.log('checkoutSession = ', checkoutSession);
        const stripePromise = loadStripe(publicKey || "");
        const stripe = await stripePromise;
    
        const result = await stripe?.redirectToCheckout({
          sessionId: checkoutSession.id
        })
    
        console.log("result = ", result)
    
      }

  return (
    <>
      <Modal show={open} onHide={handleClose} >
        <div className='bg-stone-800 px-[20px] py-[20px]'>
        <Modal.Header closeButton color='#FFFFFF'>
          <Modal.Title>
            <h1 className='text-white text-[30px]'>Your plan</h1>
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <h1 className=' text-[30px] text-white'>ChatGPT Plus <span className='text-stone-500'>USD $ 20/mo</span></h1>   
            <div className='py-[10px] bg-stone-400 rounded-[8px]'>
                <h1 className='text-gray-[700] text-center text-[30px]'>Your current plan</h1>
            </div>    
            <div className='mt-[10px]'>
                <h1 className='text-[20px] text-white'>✔Available even when demand is high</h1>
                <h1 className='text-[20px] text-white'>✔Faster response speed</h1>
                <h1 className='text-[20px] text-white'>✔Priority access to new features</h1>
            </div>   
            <div>
                <div className='mt-[20px]'>
                <a onClick={onUpgradeNow} className='cursor-pointer text-white text-[20px]'>Manage my subscription</a> 
                </div>
                <div className='mt-[20px]'>
                <a href="#" className='text-white text-[20px] mt-[30px]'>I need hlep with a billing issue</a>    
                </div>
            </div>  
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
        </div>
      </Modal>
    </>
  );
}

export default PlanModal;