import React, { useState, useEffect } from "react";
import {
  Bars3Icon,
  BellIcon,
  XMarkIcon,
  UserCircleIcon,
  PencilSquareIcon,
} from "@heroicons/react/24/outline";
import axios from "axios";
import { useParams } from "react-router";
import { useNavigate } from 'react-router-dom';
import CharacterLimitComponent from "../../components/CharacterLimitComponenet";

interface typeChatInfo {
  bot_id: number;
  instace_name:string;
  chat_name: string;
  prompt: string;
  urls: string;
  custom_text: string;
  created: string
}

interface EditBotProps {
  setButtonState: (state: boolean) => void,
}


const InstanceName = "My Website Chat";
const BotName = "Artis";
const Prompt =
  "You are a polite customer service rep and a customer asks you a question about the following content. Answer the customer's question and add a link at the end of your response if you think it will help your customer. Limit responses to no more than 50 words.";
const ChatUrls =
  "https://www.grillgrate.com/shop-by-brand/alfresco/,https://www.grillgrate.com/shop-by-brand/aog/,https://www.grillgrate.com/shop-by-brand/anova/,https://www.grillgrate.com/shop-by-brand/artisan/,https://www.grillgrate.com/shop-by-brand/aspire-by-hestan/";

const CustomContent = "My custom content for .txt files goes here";

export default function EditBot( {setButtonState} : EditBotProps ) {
  const navigate = useNavigate()
  // states for editing and saving the items in settings
  const [editingChatInstanceName, setEditingChatInstanceName] = useState(false);
  const [editingBotName, setEditingBotName] = useState(false);
  const [editingChatUrls, setEditingChatUrls] = useState(false);
  const [editingPrompt, setEditingPrompt] = useState(false);
  const [editingCustomContent, setEditingCustomContent] = useState(false);

  // states for the values of the items in settings
  const [chatInstanceName, setChatInstanceName] = useState(InstanceName);
  const [chatBotName, setChatBotName] = useState(BotName);
  const [chatUrls, setChatUrls] = useState(ChatUrls);
  const [prompt, setPrompt] = useState(Prompt);
  const [customContent, setCustomContent] = useState(CustomContent);

  // change edit to true for our settings
  const handleEditChatInstanceName = () => {
    setEditingChatInstanceName(true);
  };
  const handleEditBotName = () => {
    setEditingBotName(true);
  };
  const handleEditChatUrls = () => {
    setEditingChatUrls(true);
  };
  const handleEditPrompt = () => {
    setEditingPrompt(true);
  };

  const handleEditCustomContent = () => {
    setEditingCustomContent(true);
  };

  // handle changes to the settings

  const handleChatInstanceNameChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setChatInstanceName(event.target.value);
  };

  const handleBotNameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setChatBotName(event.target.value);
  };

  const handleChatUrlsChange = (
    event: React.ChangeEvent<HTMLTextAreaElement>
  ) => {
    setChatUrls(event.target.value);
  };

  const handlePromptChange = (
    event: React.ChangeEvent<HTMLTextAreaElement>
  ) => {
    setPrompt(event.target.value);
  };

  const handleCustomContentChange = (
    event: React.ChangeEvent<HTMLTextAreaElement>
  ) => {
    setCustomContent(event.target.value);
  };

  // handle submit for the settings
  const handleSubmitChatInstanceName = (event: React.FormEvent) => {
    event.preventDefault();
    setEditingChatInstanceName(false);
  };

  const handleSubmitBotName = (event: React.FormEvent) => {
    event.preventDefault();
    setEditingBotName(false);
  };

  const handleSubmitChatUrls = (event: React.FormEvent) => {
    event.preventDefault();
    setEditingChatUrls(false);
  };

  const handleSubmitPrompt = (event: React.FormEvent) => {
    event.preventDefault();
    setEditingPrompt(false);
  };

  const handleSubmitCustomContent = (event: React.FormEvent) => {
    event.preventDefault();
    setEditingCustomContent(false);
  };

  const { id } = useParams();

  useEffect(() => {
    if (id)
      getChatInfos(parseInt(id) - 1)
  }, [])

  const getChatInfos = async (id: number) => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getChatInfos";
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email
    }
    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      let data = []
      if (id < res.data.chats.length) {
        console.log(res.data.chats[id])
        setChatInstanceName(res.data.chats[id].instance_name)
        setChatBotName(res.data.chats[id].chat_name)
        setChatUrls(res.data.chats[id].urls)
        setPrompt(res.data.chats[id].prompt)
        
        res.data.chats[id].custom_text ? setCustomContent(res.data.chats[id].custom_text) : setCustomContent(customContent)
      } else {
        navigate('/')
      }
    }).catch((err) => {
      console.log(err)
    })
  }

  const handleSubmitPublish = async(event: React.FormEvent) => {
    event.preventDefault();
    setButtonState(true)
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email,
      instance_name: chatInstanceName,
      chat_name: chatBotName,
      prompt: prompt,
      bot_id: id,
      urls_input: chatUrls,
      custom_text: customContent === CustomContent ? "" : customContent
    }

    const path = process.env.REACT_APP_BASE_URL + "/api/updateChat";
    await axios.post(path, request, {
      headers: {
        "Content-Type": "application/json",
        withCredentials: true,
        "ngrok-skip-browser-warning": "any",
      },
    }).then((res) => {
      if(res.data === "ok")
      {
        setButtonState(false)
        window.location.reload()
      }
    }).catch((err) => {
      console.log(err)
    })
  }

  return (
    <div className="py-10 max-w-2xl w-full">
      <header>
        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <h1 className="text-xl font-bold leading-tight tracking-tight text-gray-900 text-left">
            {chatInstanceName} Settings
          </h1>
        </div>
      </header>
      <main className="mx-auto max-w-7xl py-6 sm:px-6 lg:px-8">
        <div className="shadow-sm ring-1 ring-gray-900/5 sm:rounded-xl mx-3 sm:mx-0 bg-white">
          <div className="px-4 py-6 sm:p-8 ">
            <div className="grid max-w-2xl grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            
              {/* Instance name */}
              <div className="col-span-full border-b pb-4">
                <div className="text-xs font-medium leading-6 text-gray-900 text-left">
                  Chat Instance Name
                </div>
                {editingChatInstanceName ? (
                  <div
                    className="flex items-end"
                  >
                    <input
                      className="flex w-full text-sm leading-6 text-gray-900 sm:col-span-2 sm:mt-0 text-left border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                      type="text"
                      value={chatInstanceName}
                      onChange={handleChatInstanceNameChange}
                    />
                    <button
                      className="ml-3 rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      onClick={handleSubmitChatInstanceName}
                    >
                      Save
                    </button>
                  </div>
                ) : (
                  <div className="flex justify-between items-center">
                    <div className="mt-1 flex text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                      {chatInstanceName}
                    </div>
                    <button
                      className="hover:text-indigo-500"
                      onClick={handleEditChatInstanceName}
                    >
                      <PencilSquareIcon
                        className="block h-4 w-4"
                        aria-hidden="true"
                      />
                    </button>
                  </div>
                )}
              </div>
              {/* Chatbot name */}
              <div className="col-span-full border-b pb-4">
                <div className="text-xs font-medium leading-6 text-gray-900 text-left">
                  Chatbot's Name
                </div>
                {editingBotName ? (
                  <div
                    className="flex items-end"
                  >
                    <input
                      className="flex w-full text-sm leading-6 text-gray-900 sm:col-span-2 sm:mt-0 text-left border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                      type="text"
                      value={chatBotName}
                      onChange={handleBotNameChange}
                    />
                    <button
                      className="ml-3 rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      onClick={handleSubmitBotName}
                    >
                      Save
                    </button>
                  </div>
                ) : (
                  <div className="flex justify-between items-start">
                    <div className="mt-1 flex text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0 text-left">
                      <CharacterLimitComponent text={chatBotName} limit={250} />
                    </div>
                    <button
                      className="hover:text-indigo-500 pl-5"
                      onClick={handleEditBotName}
                    >
                      <PencilSquareIcon
                        className="block h-4 w-4"
                        aria-hidden="true"
                      />
                    </button>
                  </div>
                )}
              </div>
              {/* chat prompt */}
              <div className="col-span-full border-b pb-4">
                <div className="text-xs font-medium leading-6 text-gray-900 text-left">
                  Prompt
                </div>
                {editingPrompt ? (
                  <div
                    className="flex flex-col md:flex-row items-start md:items-end"
                  >
                    <textarea
                      className="flex w-full text-sm leading-6 text-gray-900 sm:col-span-2 sm:mt-0 text-left border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                      rows={8}
                      value={prompt}
                      onChange={handlePromptChange}
                    />
                    <button
                      className="ml-0 md:ml-3 mt-3 md:mt-0 rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      onClick={handleSubmitPrompt}
                    >
                      Save
                    </button>
                  </div>
                ) : (
                  <div className="flex justify-between items-start">
                    <div className="mt-1 flex text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0 text-left">
                      <CharacterLimitComponent text={prompt} limit={250} />
                    </div>
                    <button
                      className="hover:text-indigo-500 pl-5"
                      onClick={handleEditPrompt}
                    >
                      <PencilSquareIcon
                        className="block h-4 w-4"
                        aria-hidden="true"
                      />
                    </button>
                  </div>
                )}
              </div>
              {/* chat Urls */}
              <div className="col-span-full border-b pb-4">
                <div className="text-xs font-medium leading-6 text-gray-900 text-left">
                  URLs
                </div>
                {editingChatUrls ? (
                  <div
                    className="flex flex-col md:flex-row items-start md:items-end"
                  >
                    <textarea
                      className="flex w-full text-sm leading-6 text-gray-900 sm:col-span-2 sm:mt-0 text-left border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                      rows={8}
                      value={chatUrls}
                      onChange={handleChatUrlsChange}
                    />
                    <button
                      className="ml-0 md:ml-3 mt-3 md:mt-0 rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      onClick={handleSubmitChatUrls}
                    >
                      Save
                    </button>
                  </div>
                ) : (
                  <div className="flex justify-between items-start">
                    <div className="mt-1 flex text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0 text-left">
                      <CharacterLimitComponent text={chatUrls} limit={250} />
                    </div>
                    <button
                      className="hover:text-indigo-500 pl-5"
                      onClick={handleEditChatUrls}
                    >
                      <PencilSquareIcon
                        className="block h-4 w-4"
                        aria-hidden="true"
                      />
                    </button>
                  </div>
                )}
              </div>
              {/* Custom Files */}
              <div className="col-span-full border-b pb-4">
                <div className="text-xs font-medium leading-6 text-gray-900 text-left">
                  Custom Content
                </div>
                {editingCustomContent ? (
                  <div
                    className="flex flex-col md:flex-row items-start md:items-end"
                  >
                    <textarea
                      className="flex w-full text-sm leading-6 text-gray-900 sm:col-span-2 sm:mt-0 text-left border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                      value={customContent}
                      rows={8}
                      onChange={handleCustomContentChange}
                    />
                    <button
                      className="ml-0 md:ml-3 mt-3 md:mt-0 rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      onClick={handleSubmitCustomContent}
                    >
                      Save
                    </button>
                  </div>
                ) : (
                  <div className="flex justify-between items-start">
                    <div className="mt-1 flex text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0 text-left">
                      <CharacterLimitComponent
                        text={customContent}
                        limit={250}
                      />
                    </div>
                    <button
                      className="hover:text-indigo-500 pl-5"
                      onClick={handleEditCustomContent}
                    >
                      <PencilSquareIcon
                        className="block h-4 w-4"
                        aria-hidden="true"
                      />
                    </button>
                  </div>
                )}
              </div>
            </div>
            <button
              onClick={handleSubmitPublish}
              className="rounded-md bg-indigo-600 px-3 py-2 mt-5  text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Publish Changes
            </button>
          </div>
        </div>
      </main>
    </div>
  );
}
