import { FC } from 'react';
import { BrowserRouter as Router, Navigate } from 'react-router-dom';
import { useLocation } from "react-router-dom";
import PrivateRoutes from './PrivateRoutes';
import PublicRoutes from './PublicRoutes'
import Header from '../components/Header'
import { useAuthContext } from '../store/AuthContextProvider';

const AppRouter: FC = () => {
    //   const { accessToken } = useAuth();
    const [{ authenticated }, {}] = useAuthContext();
    const location = useLocation();
    const isAuthPage =
        location.pathname === "/sign-in" || location.pathname === "/register";
    if ((localStorage.getItem('auth_email') == null) && !isAuthPage)
    {
        return <Navigate to="/sign-in" />
    }

    return (
        <>
            {!isAuthPage && <Header />}
            {/* {accessToken ? <PrivateRoutes /> : <PublicRoutes />} */}
            {authenticated ? <PrivateRoutes /> : <PublicRoutes />}
        </>

    );
};

export default AppRouter;