import React, { FC, useEffect, useState } from "react";
import Header from "../components/Header";
import axios from 'axios'
import { useNavigate } from 'react-router-dom';
import Spinner from 'react-bootstrap/Spinner';

const CreateBot: FC = () => {

  const [userEmail, setUserEmail] = useState<string>("")
  const [instanceName, setInstancename] = useState("")
  const [chatName, setChatName] = useState("");
  const [prompt, setPrompt] = useState("");
  const [urls, setUrls] = useState("");
  const [botId, setBotId] = useState(1)
  const [buttonState, setButtonState] = useState(false)
  const [totalCount, setTotalCount] = useState(1)

  const navigate = useNavigate();

  useEffect(() => {
    getInfo()
  }, [])

  const getInfo = async () => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getChatInfos";
    const user_email = localStorage.getItem('auth_email')
    setUserEmail(user_email || "")
    const request = {
      user_email: user_email
    }
    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then(async (res) => {
      if (res.data.chats.length == 0) {
        setBotId(1)
        var _totalCount = 1;
        const path = process.env.REACT_APP_BASE_URL + "/api/getSubscription";
        const user_email = localStorage.getItem('auth_email')
        const request = {
          user_email: user_email
        }
        await axios.post(path, request, {
          // headers: {
          //   "Content-Type": "application/json",
          //   withCredentials: true,
          //   "ngrok-skip-browser-warning": "any",
          // },
        }).then((res) => {
          const count = res.data;
          _totalCount = parseInt(count)
          console.log(_totalCount)
          setTotalCount(_totalCount)
        }).catch((err) => {
          console.log(err)
        })
        console.log("totalCount = ", _totalCount)
      } else {
        var _botId = 0;
        var _totalCount = 1;
        const path = process.env.REACT_APP_BASE_URL + "/api/getSubscription";
        const user_email = localStorage.getItem('auth_email')
        const request = {
          user_email: user_email
        }
        await axios.post(path, request, {
          // headers: {
          //   "Content-Type": "application/json",
          //   withCredentials: true,
          //   "ngrok-skip-browser-warning": "any",
          // },
        }).then((res) => {
          const count = res.data;
          _totalCount = parseInt(count)
          console.log(_totalCount)
          setTotalCount(_totalCount)
        }).catch((err) => {
          console.log(err)
        })
        console.log("totalCount = ", _totalCount)
        outerloop:
        for (var i = 1; i <= _totalCount; i++) {
          console.log("index = ", i)
          var flag = 0;
          for (var j = 0; j < res.data.length; j++) {
            console.log("exist = ", res.data[j].bot_id)
            if (i === res.data[j].bot_id) {
              flag = 1;
              console.log("bot_id=", i);
              break;
            }
          }
          console.log("flag = ", flag)
          if (flag == 0) {
            _botId = i;
            setBotId(i);
            break outerloop;
          }
        }
        console.log(_botId)
        if (_botId == 0) {
          navigate('/')
        }
      }
    }).catch((err) => {
      console.log(err)
    })
  }

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {

    e.preventDefault();
    console.log("instanceName = ", instanceName);
    console.log("chatName = ", chatName);
    console.log("prompt = ", prompt);
    console.log("urls = ", urls);

    if (instanceName !== "" || chatName !== "" || prompt !== "" || urls !== "") {
      setButtonState(true)
      alert('Please wait now I am amking the chatbot')

      const _path = process.env.REACT_APP_BASE_URL + "/api/getSubscription";
      const user_email = localStorage.getItem('auth_email')
      const _request = {
        user_email: user_email
      }
      let _totalCount = 1;
      let _urls = urls
      await axios.post(_path, _request, {
        // headers: {
        //   "Content-Type": "application/json",
        //   withCredentials: true,
        //   "ngrok-skip-browser-warning": "any",
        // },
      }).then((res) => {
        const count = res.data;
        _totalCount = parseInt(count)
      }).catch((err) => {
        console.log(err)
      })
      console.log("totalCount = ", _totalCount)

      if (_totalCount === 1) {
        _urls = ''
        let arr = urls.split(',', 2);
        _urls = arr[0] + ", " + arr[1]
        setUrls(_urls)
      }

      console.log("urls = ", _urls)

      const path = process.env.REACT_APP_BASE_URL + "/api/newChat";
      const request = {
        instace_name: instanceName,
        user_email: userEmail,
        chat_name: chatName,
        prompt: prompt,
        bot_id: botId,
        urls_input: _urls
      }

      await axios.post(path, request, {
        // headers: {
        //   "Content-Type": "application/json",
        //   withCredentials: true,
        //   "ngrok-skip-browser-warning": "any",
        // },
      }).then((res) => {
        console.log(res.data)
        if (res.data == "ok")
          navigate('/')
        setButtonState(false)
      }).catch((err) => {
        console.log(err)
        setButtonState(false)
      })
    }
  }

  const onChangeUrl = async (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    console.log(event.target.value)
    let _urls = event.target.value

    if (totalCount === 1 && event.target.value.includes(',')) {
      _urls = ''
      let arr = event.target.value.split(',', 2);
      _urls = arr[0] + "," + arr[1]
      setUrls(_urls)
    }
    else 
    {
      console.log(event.target.value)
      setUrls(event.target.value)
    }

  }

  return (
    <div className="relative">
      {buttonState && <div className="absolute w-full h-full flex items-center justify-center z-10"> <Spinner className="" /> </div>}
      <div className={"py-10 " + (buttonState && "backdrop-blur-md backdrop-brightness-50 bg-white/30")}>
        <header>
          <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
            <h1 className="text-3xl font-bold leading-tight tracking-tight text-gray-900">
              Create Chat
            </h1>
          </div>
        </header>
        <main className="mx-auto max-w-7xl py-10 sm:px-6 lg:px-8">
          <div className="shadow-sm ring-1 ring-gray-900/5 sm:rounded-xl mx-3 sm:mx-0">
            <form onSubmit={handleSubmit}>
              <div className="px-4 py-6 sm:p-8">
                <div className="grid max-w-2xl grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                  <div className="col-span-full">
                    <label
                      htmlFor="about"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Chat Instance Name
                    </label>
                    <div className="mt-2">
                      <input
                        id="open_ai_api_key"
                        name="open_ai_api_key"
                        onChange={(e) => {
                          // setChatName(e.target.value)
                          setInstancename(e.target.value)
                        }}
                        placeholder="Instance Chat Name"
                        className="block w-full rounded-md border-0 px-3 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                      />
                    </div>
                    <p className="mt-1 mb-5 text-xs  leading-2 text-gray-600">
                      Something that will help you remember what this chat is
                      about, such as {`{Website Name}`} Chat.
                    </p>
                  </div>

                  <div className="col-span-full">
                    <label
                      htmlFor="about"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Chat bot name
                    </label>
                    <div className="mt-2">
                      <input
                        id="open_ai_api_key"
                        name="open_ai_api_key"
                        onChange={(e) => {
                          setChatName(e.target.value)
                        }}
                        placeholder="My Awesome Chat"
                        className="block w-full rounded-md border-0 px-3 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                      />
                    </div>
                    <p className="mt-1 mb-5 text-xs  leading-2 text-gray-600">
                      Something that will help you remember what this chat is
                      about, such as {`{Website Name}`} Chat.
                    </p>
                  </div>
                  <div className="col-span-full">
                    <label
                      htmlFor="about"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Prompt
                    </label>
                    <div className="mt-2">
                      <textarea
                        id="prompt"
                        name="prompt"
                        placeholder="You are a polite customer service rep for {insert company name} and a customer asks you a question about the following content. Answer the customer's question and add a link at the end of your response if you think it will help your customer. Limit responses to no more than 50 words."
                        rows={3}
                        onChange={(e) => {
                          setPrompt(e.target.value)
                        }}
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                      />
                    </div>
                    <p className="mt-1 mb-5 text-xs  leading-2 text-gray-600">
                      Create your prompt for guiding your AI. Don't worry, you
                      can always change this later.
                    </p>
                  </div>
                  <div className="col-span-full">
                    <label
                      htmlFor="about"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Enter URL's separated by a comma
                    </label>
                    <div className="mt-2">
                      <textarea
                        id="urls"
                        name="urls"
                        placeholder="https://www.example.com,https://www.example.com"
                        rows={3}
                        onChange={onChangeUrl}
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        value={urls}
                      />
                    </div>
                    <p className="mt-1 mb-5 text-xs leading-2 text-gray-600">
                      These are the urls that will be used to train your AI.
                      Make sure your urls are valid and comma seperated and that
                      you have permission to use them. Be sure to include
                      https:// or http:// or the AI will not be able to find
                      them.
                    </p>
                    <button
                      type="submit"
                      disabled={buttonState}
                      className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                      Train my AI
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </main>
      </div>
    </div>
  );
}

export default CreateBot;
