import { FC, useEffect } from 'react';
import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import App from '../App';
import Account from '../Account'
import CreateBot from '../pages/CreateBot'
import ExampleBot from '../pages/ExampleBot'
import { useAuthContext } from '../store/AuthContextProvider';
import Edit from '../pages/edit/Edit';

const PrivateRoutes: FC = () => {
  const navigate = useNavigate();

  const [{ authenticated }, {}] = useAuthContext();
  const location = useLocation();
  useEffect(() => {
    const isAuthPage =
      location.pathname === "/" || location.pathname === "/create" || location.pathname === "/account" || location.pathname.includes("/example-bot/") || location.pathname.includes("/edit-bot/") ;;
    console.log(location.pathname)
    if (authenticated == true && !isAuthPage == true) {
      navigate('/')
    }
  }, [location.pathname])
  return (
    <Routes>
      <Route path="/" element={<App />} />
      <Route path="/create" element={<CreateBot />} />
      <Route path="/account" element={<Account />} />
      <Route path="/example-bot/:id" element={<ExampleBot />} />
      <Route path='/edit-bot/:id' element={<Edit />} />
    </Routes>
  );
};

export default PrivateRoutes;
