import React, { useState, useEffect } from "react";
import axios from 'axios'
import { v4 as uuidv4 } from "uuid";
import { PaperAirplaneIcon, TrashIcon } from "@heroicons/react/20/solid";
import Spinner from 'react-bootstrap/Spinner';
import "../App.css";
import { useParams } from "react-router";
import { useNavigate } from 'react-router-dom';

interface Message {
  id: string;
  content: string;
  sender: string;
}

interface ChatMessageProps {
  message: Message;
  botName: string;
}

interface typeChatInfo {
  bot_id: number;
  instance_name: string;
  chat_name: string;
  chats: Array<any>;
  created: string
}

const ChatMessage: React.FC<ChatMessageProps> = ({ message, botName }) => {
  return (
    <div className={`chat-message ${message.sender}`}>
      {message.sender === "bot" && (
        <div className="avatar">
          <img
            src="/assets/image/robot.jpg"
            alt="avatar"
          />
          <span className="botname">{botName}</span>
        </div>
      )}
      <div className="message-content" dangerouslySetInnerHTML={{ __html: message.content }}></div>
    </div>
  );
};

interface ChatInputProps {
  onSubmit: (message: string) => void;
  pendingState: Boolean
}

const ChatInput: React.FC<ChatInputProps> = ({ onSubmit, pendingState }) => {
  const [message, setMessage] = useState("");

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (message.trim()) {
      onSubmit(message.trim());
      setMessage("");
    }
  };

  return (
    <div className="chat-input-wrapper">
      <form className="chat-input" onSubmit={handleSubmit}>
        <input
          type="text"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          placeholder="Type a message..."
        />
        <button type="submit" disabled={!!pendingState}>
          {pendingState ? <Spinner className="-ml-0.5 mr-1.5 h-5 w-[15px]" size="sm" animation="border" variant="light" /> : <PaperAirplaneIcon
            className="-ml-0.5 mr-1.5 h-5 w-[15px]"
            aria-hidden="true"
          />}
        </button>
      </form>
    </div>
  );
};

export default function ExampleBot() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [pendingState, setPendingState] = useState<Boolean>(false)
  const [userInfo, setUserInfo] = useState<typeChatInfo>({
    bot_id: 0,
    instance_name: '',
    chat_name: '',
    chats: [],
    created: ''
  });
  const navigate = useNavigate();

  const { id } = useParams();

  useEffect(() => {
    if (id)
      getChatInfos(parseInt(id) - 1)
  }, [])

  const getChatInfos = async (id: number) => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getChatInfos";
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email
    }
    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      let data = []
      if (id < res.data.chats.length) {
        console.log(res.data.chats[id])
        setUserInfo(res.data.chats[id])
        if (res.data.chats[id].chats.length > 1)
          for (var i = 1; i < res.data.chats[id].chats.length; i++) {
            data.push({
              id: uuidv4(),
              content: res.data.chats[id].chats[i].question,
              sender: 'user'
            })
            data.push({
              id: uuidv4(),
              content: res.data.chats[id].chats[i].answer,
              sender: 'bot'
            })
          }
        setMessages(data)
      } else {
        navigate('/')
      }
    }).catch((err) => {
      console.log(err)
    })
  }

  const addMessage = (content: string) => {
    setPendingState(true)
    console.log(messages);
    let responseMessage = "";
    setMessages((prevMessages) => [
      ...prevMessages,
      { id: uuidv4(), content, sender: "user" },
    ]);

    // Simulate a response
    setTimeout(async () => {
      console.log(content)
      const path = process.env.REACT_APP_BASE_URL + "/api/chat";
      const user_email = localStorage.getItem('auth_email')
      const request = {
        user_email: user_email,
        bot_id: userInfo.bot_id,
        message_text: content
      }

      await axios.post(path, request, {
        // headers: {
        //   "Content-Type": "application/json",
        //   withCredentials: true,
        //   "ngrok-skip-browser-warning": "any",
        // },
      }).then((res) => {
        console.log(res.data);
        responseMessage = res.data.response
      })

      setPendingState(false)
      setMessages((prevMessages) => [
        ...prevMessages,
        { id: uuidv4(), content: responseMessage, sender: "bot" },
      ]);
    }, 1000);
  };

  const deleteChats = async () => {
    setMessages([]);
    const path = process.env.REACT_APP_BASE_URL + "/api/chatsDelete";
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email,
      bot_id: userInfo.bot_id,
    }

    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      console.log(res.data);
    })
  }

  return (
    <div className="App">
      <div className="chat-container">
        <div className="chat-header">
          {/* <div className="ai-avatar">
            <img
              src="/assets/image/robot.jpg"
              alt="avatar"
            />
          </div>
          <div className="ai-name">
            <h3>{userInfo.chat_name}</h3>
            <div className="ai-status">
              online
              <span className="ai-status-circle"></span>
            </div>
          </div> */}
          {userInfo.chat_name}
          <TrashIcon className="w-[20px] cursor-pointer" onClick={deleteChats} />
        </div>
        <div className="chat-inner">
          <div className="chat-messages">
            {messages.map((message) => (
              <ChatMessage key={message.id} message={message} botName={userInfo.chat_name} />
            ))}
          </div>
        </div>
        <ChatInput onSubmit={addMessage} pendingState={pendingState} />
      </div>
    </div>
  );
}
