import { Menu, Transition } from "@headlessui/react";
import { EllipsisVerticalIcon } from "@heroicons/react/20/solid";
import { PlusIcon } from "@heroicons/react/20/solid";
import { loadStripe } from '@stripe/stripe-js';
import axios from 'axios'
import React, { useEffect, useState } from "react";
import { Fragment } from "react";
import { Link } from "react-router-dom";
import "./App.css";
import 'bootstrap/dist/css/bootstrap.min.css';

const publicKey = process.env.REACT_APP_STRIPE_PUBLISH_KEY;
const secretKey = process.env.REACT_APP_STRIPE_SECRET_KEY;
console.log(secretKey)
const stripeCheckoutSession = require('stripe')(secretKey as string)

function classNames<T extends string>(...classes: T[]): string {
  return classes.filter(Boolean).join(" ");
}

const bots = [
  {
    id: 1,
    name: "My Chat One Name",
    href: "#",
    DateCreated: "May 9, 2023",
  },
  {
    id: 2,
    name: "My Bot Two Name",
    href: "#",
    DateCreated: "April 22, 2023",
  },
  {
    id: 3,
    name: "Bot Three Name",
    href: "#",
    DateCreated: "March 7, 2023",
  },
  {
    id: 4,
    name: "Bot Name Four",
    href: "#",
    DateCreated: "January 18, 2023",
  },
];

interface typeChatInfo {
  bot_id: number;
  instance_name: string;
  chat_name: string;
  chats: Array<any>;
  complete: boolean;
  created: string;
}

export default function App() {

  const [chatInfos, setChatInfos] = useState<typeChatInfo[]>([])
  const [totalCount, setTotalCount] = useState(1)

  useEffect(() => {
    getTotalCount()
    getChatInfos()
  }, [])

  const getChatInfos = async () => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getChatInfos";
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email
    }
    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      const _chatInfos = res.data.chats;
      setChatInfos(_chatInfos)
    }).catch((err) => {
      console.log(err)
      setChatInfos([])
    })
  }

  const getTotalCount = async () => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getSubscription";
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email
    }
    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      const count = res.data;
      setTotalCount(parseInt(count))
    }).catch((err) => {
      console.log(err)
      setTotalCount(1)
    })
  }

  const onDeleteBot = async (id: number) => {
    const path = process.env.REACT_APP_BASE_URL + "/api/botDelete";
    const user_email = localStorage.getItem('auth_email')
    const request = {
      user_email: user_email,
      bot_id: id
    }

    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      console.log(res);
      getChatInfos();
    }).catch(err => {
      console.log(err)
    })
  }

  const onUpgradeNow = async () => {
    console.log("------------upgarde--------------------")
    const email = localStorage.getItem('auth_email')
    const checkoutSession = await stripeCheckoutSession.checkout.sessions.create({
      line_items: [
        {
          price_data: {
            currency: 'USD',
            product_data: {
              name: "Label Purchase",
            },
            unit_amount: 100,
          },
          quantity: 25,
        },
      ],
      mode: "payment",
      success_url: 'https://chatsavvy.ai/',
      cancel_url: 'https://chatsavvy.ai/',
      customer_email: email,
    });
    console.log('checkoutSession = ', checkoutSession);
    const stripePromise = loadStripe(publicKey || "");
    const stripe = await stripePromise;

    const result = await stripe?.redirectToCheckout({
      sessionId: checkoutSession.id
    })

    console.log("result = ", result)

  }

  const onUnsunscribe = async() => {
    const email = localStorage.getItem('auth_email')
    const request = {
      user_email: email,
    }

    const path = process.env.REACT_APP_BASE_URL + "/api/unSubscribe";
    await axios.post(path, request, {
      // headers: {
      //   "Content-Type": "application/json",
      //   withCredentials: true,
      //   "ngrok-skip-browser-warning": "any",
      // },
    }).then((res) => {
      console.log(res);
      getChatInfos();
    }).catch(err => {
      console.log(err)
    })
  }

  return (
    <div>
      <div className="py-10">
        <header>
          <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8 ">
            <h1 className="text-3xl font-bold leading- tight tracking-tight text-gray-900">
              Dashboard
            </h1>
          </div>
        </header>
        <main className="mx-auto max-w-7xl py-10 sm:px-6 lg:px-8 px-4">
          <div className="border  border-gray-100 sm:px-12 px-3 py-12">

            {/* Show below if no bots created */}
            {chatInfos.length == 0 && <div className="text-center">
              <h3 className="mt-2 text-lg font-bold text-gray-900">
                No Chats Created
              </h3>
              <p className="mt-1 text-sm text-gray-500">
                Get started by creating your first chat!
              </p>
              <div className="mt-6">
                <Link to="/create">
                  <button
                    type="button"
                    className="inline-flex items-center rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                  >
                    <PlusIcon
                      className="-ml-0.5 mr-1.5 h-5 w-5"
                      aria-hidden="true"
                    />
                    New Chat
                  </button>
                </Link>
              </div>
            </div>}
            {/* End show if no bots created  */}

            {chatInfos.length > 0 && <><div className="flex items-center justify-between border-b border-gray-100 pb-10">
              <div>
                <h2 className="text-lg font-bold leading-6 text-gray-800">
                  {chatInfos.length} of {totalCount} Chats Created{" "}
                  <span className="text-sm text-gray-500  font-normal">
                    ({totalCount - chatInfos.length} remaining)
                  </span>
                </h2>
                {(totalCount === 1 || chatInfos.length >= totalCount) && <p className="mt-1 text-sm text-gray-500">
                  Need more bots?{" "}
                  <a
                    className="text-indigo-600 hover:text-indigo-500 ml-1 font-semibold cursor-pointer"
                    onClick={onUpgradeNow}
                  >
                    Upgrade now
                  </a>
                </p>}
              </div>
              <div className="flex gap-2">
              {/* {totalCount > 1 &&
                  <button
                    type="button"
                    onClick={onUnsunscribe}
                    className="inline-flex items-center rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                  >
                    Unsubscribe
                  </button>} */}

              {(chatInfos.length < totalCount) &&
                <Link to="/create">
                  <button
                    type="button"
                    className="inline-flex items-center rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                  >
                    <PlusIcon
                      className="-ml-0.5 mr-1.5 h-5 w-5"
                      aria-hidden="true"
                    />
                    New Chat
                  </button>
                </Link>
              }
              </div>
            </div>

              <ul role="list" className="divide-y divide-gray-100">
                {chatInfos.map((bot: typeChatInfo, index: number) => <li
                    key={index}
                    className="flex items-center justify-between gap-x-6 py-5"
                  >
                    <div className="min-w-0">
                      <div className="flex items-start gap-x-3">
                        <p className="text-sm font-semibold leading-6 text-gray-900">
                          {bot.instance_name}
                        </p>
                      </div>
                      <div className="mt-1 flex items-center gap-x-2 text-xs leading-5 text-gray-500">
                        <p className="truncate">Updated: {bot.created}</p>
                      </div>
                    </div>
                    <div className="flex flex-none items-center gap-x-4">
                    {bot.complete ? <Link to={'/example-bot/' + (index + 1)} className="hidden rounded-md bg-white px-2.5 py-1.5 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:block">
                        Preview
                        <span className="sr-only">, {bot.chat_name}</span>
                      </Link> : <h1 className="text-[12px]">{bot.chat_name} is being created. Check back momentarily</h1>}
                      <Menu as="div" className="relative flex-none">
                        <Menu.Button className="-m-2.5 block p-2.5 text-gray-500 hover:text-gray-900">
                          <span className="sr-only">Open options</span>
                          <EllipsisVerticalIcon
                            className="h-5 w-5"
                            aria-hidden="true"
                          />
                        </Menu.Button>
                        <Transition
                          as={Fragment}
                          enter="transition ease-out duration-100"
                          enterFrom="transform opacity-0 scale-95"
                          enterTo="transform opacity-100 scale-100"
                          leave="transition ease-in duration-75"
                          leaveFrom="transform opacity-100 scale-100"
                          leaveTo="transform opacity-0 scale-95"
                        >
                          <Menu.Items className="absolute right-0 z-10 mt-2 w-32 origin-top-right rounded-md bg-white py-2 shadow-lg ring-1 ring-gray-900/5 focus:outline-none">
                            <Menu.Item>
                              {({ active }) => (
                                bot.complete ? 
                                <a
                                  href="#"
                                  className={classNames(
                                    active ? "bg-gray-50" : "",
                                    "px-3 py-1 text-sm leading-6 text-gray-900 block sm:hidden"
                                  )}
                                >
                                  Preview
                                  <span className="sr-only">, {bot.chat_name}</span>
                                </a> : <h1 className={classNames(
                                    active ? "bg-gray-50" : "",
                                    "px-3 py-1 text-sm leading-6 text-gray-900 block sm:hidden"
                                  )}>{bot.chat_name} is being created. Check back momentarily</h1>
                              )}
                            </Menu.Item>
                            <Menu.Item>
                              {({ active }) => (
                                <Link
                                  to={'/edit-bot/' + (index + 1)}
                                  className={classNames(
                                    active ? "bg-gray-50" : "",
                                    "block px-3 py-1 text-sm leading-6 text-gray-900"
                                  )}
                                >
                                  Edit
                                  <span className="sr-only">, {bot.chat_name}</span>
                                </Link>
                              )}
                            </Menu.Item>

                            <Menu.Item>
                              {({ active }) => (
                                <a
                                  onClick={() => { onDeleteBot(bot.bot_id) }}
                                  className={classNames(
                                    active ? "bg-gray-50" : "",
                                    "block px-3 py-1 text-sm leading-6 text-gray-900 cursor-pointer"
                                  )}
                                >
                                  Delete
                                  <span className="sr-only">, {bot.chat_name}</span>
                                </a>
                              )}
                            </Menu.Item>
                          </Menu.Items>
                        </Transition>
                      </Menu>
                    </div>
                  </li>
                )}
              </ul></>}
          </div>
        </main>
      </div>
    </div>
  );
}
